import { MAX_TITLE_LENGTH } from '../constants';
import {
  FilterSectionRadioConfigItem,
  FilterSectionRadioItem
} from './filterSectionRadioTypes';

/** Преобразовать конфигурацию в выбранное значение фильтра */
export function convertConfigItemToFilter(
  src: FilterSectionRadioConfigItem
): FilterSectionRadioItem {
  const result: FilterSectionRadioItem = {
    filterId: src.filterId,
    value: src.value,
    label: src.label,
    mode: src.defaultMode || src.modes[0].value
  };

  return result;
}

/** Обрезать текст до необходимой длины */
export function trimText(text: string): string {
  return text.length <= MAX_TITLE_LENGTH
    ? text
    : `${ text.substring(0, MAX_TITLE_LENGTH) }...`;
}
